import { Box, Chip, LinearProgress, LinearProgressProps, Link, Paper, Skeleton, Typography, useMediaQuery } from "@mui/material";
import React, { useEffect, useState } from "react";
import { Link as RouterLink } from "react-router-dom";
import { CommonConstants } from "vitepunks-common";
import { CommonContext, getCommonContext } from "../../../../contexts/common.context";
import { usePunkById } from "../../../../hooks/punk.hook";
import theme from "../../../../theme";
import { MomentUtil } from "../../../../util/moment.util";
import { Countdown } from "../../../common/components/countdown";
import { AddressExplorerLink } from "../../../common/components/explorer-link";
import { PunkImageCard } from "../../../punk/components/image-common";

interface LinearProgressWithLabelProps extends LinearProgressProps {
  address: string
  label: string
}

const LinearProgressWithLabel: React.FC<LinearProgressWithLabelProps> = (props: LinearProgressWithLabelProps) => {
  const matches = useMediaQuery(theme.breakpoints.up("md"));

  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ display: "flex", alignItems: "center" }}>
        {matches && (
          <Box sx={{ mr: 1, minWidth: "115px" }}>
            <AddressExplorerLink address={props.address}></AddressExplorerLink>
          </Box>
        )}
        <Box sx={{ width: "100%", mr: 1 }}>
          <LinearProgress variant="determinate" sx={{ height: 5 }} {...props} />
        </Box>
        <Box sx={{ minWidth: 35 }}>
          <Typography variant="body2" color="text.secondary">{props.label}</Typography>
        </Box>
      </Box>
      {!matches && (
        <AddressExplorerLink address={props.address}></AddressExplorerLink>
      )}
    </Box>
  );
}

type PunkHolder = {
  address: string
  total: number
  rares: number
}

export const LandingRanking = () => {
  const mounted = React.useRef(false);
  const punkId = "6297";
  const rankThreshold = 100;
  const moment = React.useMemo<MomentUtil>(() => {
    return new MomentUtil();
  }, []);
  const context = React.useMemo<CommonContext>(() => {
    return getCommonContext();
  }, []);
  const [isLoading, setIsLoading] = useState(false);
  const [maxRares] = useState(19);
  const [ranks, setRanks] = useState<PunkHolder[]>([]);
  const { punk } = usePunkById(punkId);
  const date = moment.getFromUnix(1643673599);
  const matches = useMediaQuery(theme.breakpoints.up("md"));

  useEffect(() => {
    mounted.current = true;
    return () => {
      mounted.current = false;
    };
  }, []);

  useEffect(() => {
    const setRanksAsync = async () => {
      const holders: PunkHolder[] = [
        {
          "address": "vite_caaa8d1637482a84e6b4f6af356c176549380ce474669ffb01",
          "total": 824,
          "rares": 19
        },
        {
          "address": "vite_abd148025d2fe9ea1fd5643574166022ea0b0805cacc051fb9",
          "total": 594,
          "rares": 12
        },
        {
          "address": "vite_aea4e30235ea1782876e066c0c604ef8e15f47b1f4ac8772d1",
          "total": 390,
          "rares": 3
        },
        {
          "address": "vite_ceac16d888ad4240c6e345c961bc306e80bf5dc6cdd3432a18",
          "total": 134,
          "rares": 3
        },
        {
          "address": "vite_94c2bd05c387d247b5d1b7b3083162456f8c1e2b7493ff3802",
          "total": 207,
          "rares": 2
        },
        {
          "address": "vite_fbad469e039e9c44559b8ef6043dbf8c46f117c01cc7f7d44d",
          "total": 104,
          "rares": 2
        },
        {
          "address": "vite_e145dcc8c0a93012aa71ebedb03dd251e6ebd57142cb312541",
          "total": 151,
          "rares": 1
        }
      ]
      if (mounted) {
        setRanks(holders);
        setIsLoading(false);
      }
    }
    setIsLoading(true);
    setTimeout(() => {
      setRanksAsync();
    }, 2000);
  }, [context.datasource]);

  return (
    <Box display="flex" justifyContent="center" alignItems="center">
      <Box maxWidth={900}>
        <Paper
          style={{
            backgroundColor: "rgba(255, 255, 255, 0.5)"
          }}
        >
          <Box sx={{ display: matches ? "flex" : "inherit", px: 2, pt: 2, pb: 2, width: "100%" }}>
            <PunkImageCard punkId={punkId}></PunkImageCard>
            <Box sx={{ flexGrow: 1, ml: matches ? 2 : 0, mt: matches ? 0 : 2 }}>
              <Typography variant="h6" paragraph>💎 Rarity contest</Typography>
              The address owning the most rare punks below rank {rankThreshold} when the contest ends will be
              awarded <Link underline="hover" component={RouterLink} to={`/punk/${punkId}`} target="_blank">{CommonConstants.PUNK_NAME} #{punkId}</Link> with
              rarity rank <Chip size="small" label={punk?.rank} />
              <br></br>
              In case 2 or more addresses own the same amount of rare punks, the address with overall more punks will be awarded.
              <Box sx={{ mt: 3 }}>
                {isLoading ? (
                  <>
                    <Skeleton animation="wave"></Skeleton>
                    <Skeleton animation="wave" sx={{ mt: 0.5 }}></Skeleton>
                    <Skeleton animation="wave" sx={{ mt: 0.5 }}></Skeleton>
                    <Skeleton animation="wave" sx={{ mt: 0.5 }}></Skeleton>
                    <Skeleton animation="wave" sx={{ mt: 0.5 }}></Skeleton>
                    <Skeleton animation="wave" sx={{ mt: 0.5 }}></Skeleton>
                  </>
                ) : (
                  <>
                    {ranks.map((e: PunkHolder) => (
                      <LinearProgressWithLabel key={e.address} value={e.rares / maxRares * 100} label={e.rares.toString()} address={e.address}></LinearProgressWithLabel>
                    ))}
                  </>
                )}
              </Box>
              <Paper elevation={3} sx={{ p: 2, textAlign: "center", mt: 3 }}>
                <Countdown date={date.toISOString()}></Countdown>
              </Paper>
            </Box>
          </Box>
        </Paper>
      </Box>
    </Box>
  );
}
