import { orderBy, uniqBy } from "lodash";
import { CommonConstants, CommonUtil, PunkTypes, TransformUtil } from "vitepunks-common";
import { CommonContext, getCommonContext } from "../contexts/common.context";

export type ContestItem = {
  isPlaceholder: boolean
  address: string
  punkId: number
  rank: number
}

export class ContestService {
  private readonly _context: CommonContext;

  constructor() {
    this._context = getCommonContext();
  }

  async getRankingsAsync(startTimestamp: number, endTimestamp: number, order: PunkTypes.OrderBy): Promise<ContestItem[]> {
    const _startTimestamp = TransformUtil.toBigNumber(startTimestamp.toString());
    const _endTimestamp = TransformUtil.toBigNumber(endTimestamp.toString());
    const _result = await this._context.datasource.getMarketEventsByTypeAsync(PunkTypes.PunkEventType.Mint.toString(), CommonConstants.TOTAL_MINTS, 0);
    const _items: ContestItem[] = [];
    for (const e of _result.events) {
      if (e.timestamp.lt(_startTimestamp) || e.timestamp.gt(_endTimestamp)) {
        continue;
      }
      if (e.toAddress && !CommonUtil.isNullOrWhitespace(e.toAddress)) {
        const punk = await this._context.datasource.getPunkAsync(e.punkId.toString());
        _items.push({
          isPlaceholder: false,
          address: e.toAddress,
          punkId: e.punkId,
          rank: punk.rank
        })
      }
    }
    const _itemsOrdered = orderBy(
      _items,
      [
        (e: ContestItem) => e.rank
      ],
      [
        order === PunkTypes.OrderBy.ASC ? "asc" : "desc"
      ]
    );

    const _itemsFilterd = uniqBy(
      _itemsOrdered,
      (e: ContestItem) => e.address
    );
    return _itemsFilterd;
  }
}

const service = new ContestService();

export const getContestService = () => {
  return service;
}