import { Link } from '@mui/material';
import React, { useCallback, useEffect, useState } from 'react';
import { CommonUtil, ExplorerUtil } from "vitepunks-common";
import { ExtendedBootstrapTooltip } from '../tooltip';

interface Props {
  children: React.ReactNode
  value?: Maybe<string>
  type: ExplorerLinkType
}

export enum ExplorerLinkType {
  Address,
  Transaction
}

export const ExplorerLink: React.FC<Props> = (props: Props) => {
  const getUrl = useCallback(() => {
    if (!props.value || CommonUtil.isNullOrWhitespace(props.value)) {
      return undefined;
    }
    switch (props.type) {
      case ExplorerLinkType.Address:
        return ExplorerUtil.getAddressUrl(props.value);
      default:
        return ExplorerUtil.getTxUrl(props.value);
    }
  }, [props.value, props.type]);
  const [url, setUrl] = useState<Maybe<string>>(getUrl());

  useEffect(() => {
    setUrl(getUrl());
  }, [props.value, props.type, getUrl]);

  if (!url || CommonUtil.isNullOrWhitespace(url)) {
    return (
      <>
        {props.children}
      </>
    )
  }

  return (
    <Link underline="hover" href={url} target="_blank">
      {props.children}
    </Link>
  );
}

interface AddressExplorerLinkProps {
  address: string
  startPosition?: Maybe<number>
}

export const AddressExplorerLink: React.FC<AddressExplorerLinkProps> = (props: AddressExplorerLinkProps) => {
  const getAddressUrl = () => {
    return ExplorerUtil.getAddressUrl(props.address);
  }
  const truncateAddress = () => {
    return CommonUtil.truncateStringInTheMiddle(props.address, props.startPosition ? props.startPosition : 10, 5);
  }
  return (
    <ExtendedBootstrapTooltip title={props.address} placement="bottom" arrow>
      <Link underline="hover" href={getAddressUrl()} target="_blank" variant="body2" color="text.secondary">{truncateAddress()}</Link>
    </ExtendedBootstrapTooltip>
  )
}
