import { Box, Chip, Link, Paper, Skeleton, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography, useMediaQuery } from "@mui/material";
import React, { useEffect, useState } from "react";
import { Link as RouterLink } from "react-router-dom";
import { CommonConstants, CommonUtil, PunkTypes } from "vitepunks-common";
import { usePunkById } from "../../../../hooks/punk.hook";
import { ContestItem, ContestService, getContestService } from "../../../../services/contest.service";
import theme from "../../../../theme";
import { MomentUtil } from "../../../../util/moment.util";
import { Countdown } from "../../../common/components/countdown";
import { AddressExplorerLink } from "../../../common/components/explorer-link";
import { PunkImageCard } from "../../../punk/components/image-common";

interface Props {
  punkId: string
  asc: boolean
  startTimestamp: number
  endTimestamp: number
}

export const LandingContest: React.FC<Props> = (props: Props) => {
  const mounted = React.useRef(false);
  const title = "Bi-weekly contest";

  const createPlaceholder = React.useCallback((): ContestItem => {
    return {
      isPlaceholder: true,
      address: "",
      punkId: -1,
      rank: -1
    }
  }, []);
  const moment = React.useMemo<MomentUtil>(() => {
    return new MomentUtil();
  }, []);
  const service = React.useMemo<ContestService>(() => {
    return getContestService();
  }, []);
  const [isLoading, setIsLoading] = useState(false);
  const [ranks, setRanks] = useState<ContestItem[]>([
    createPlaceholder(),
    createPlaceholder(),
    createPlaceholder()
  ]);
  const { punk } = usePunkById(props.punkId);
  const date = moment.getFromUnix(props.endTimestamp);
  const matches = useMediaQuery(theme.breakpoints.up("md"));

  useEffect(() => {
    mounted.current = true;
    return () => {
      mounted.current = false;
    };
  }, []);

  useEffect(() => {
    const setRanksAsync = async () => {
      const events = await service.getRankingsAsync(props.startTimestamp, props.endTimestamp, props.asc ? PunkTypes.OrderBy.ASC : PunkTypes.OrderBy.DESC);
      if (mounted) {
        setRanks([
          events.length >= 1 ? events[0] : createPlaceholder(),
          events.length >= 2 ? events[1] : createPlaceholder(),
          events.length >= 3 ? events[2] : createPlaceholder()
        ]);
        setIsLoading(false);
      }
    }
    setIsLoading(true);
    setTimeout(() => {
      setRanksAsync();
    }, 2000);
  }, [props, service, createPlaceholder]);

  return (
    <Box display="flex" justifyContent="center" alignItems="center">
      <Box maxWidth={900}>
        <Paper
          style={{
            backgroundColor: "rgba(255, 255, 255, 0.5)"
          }}
        >
          <Box sx={{ display: matches ? "flex" : "inherit", px: 2, pt: 2, pb: 2, width: "100%" }}>
            <PunkImageCard punkId={props.punkId}></PunkImageCard>
            <Box sx={{ flexGrow: 1, ml: matches ? 2 : 0, mt: matches ? 0 : 2 }}>
              <Typography variant="h6" paragraph>💎 {title}</Typography>
              The address minting a punk with the <u>{props.asc ? 'lowest' : 'highest'}</u> rarity rank during this {title.toLowerCase()} will be
              awarded <Link underline="hover" component={RouterLink} to={`/punk/${props.punkId}`} target="_blank">{CommonConstants.PUNK_NAME} #{props.punkId}</Link> with
              rarity rank <Chip size="small" label={punk?.rank} /> when the contest ends.
              <TableContainer component={Box} sx={{ mt: 2 }}>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell></TableCell>
                      <TableCell>Address</TableCell>
                      <TableCell align="right">Punk</TableCell>
                      <TableCell align="right">Rarity</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {ranks.map((e: ContestItem, index: number) => (
                      <TableRow
                        key={CommonUtil.uuid()}
                        sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                      >
                        <TableCell component="th" scope="row">
                          {index === 0 && "🏆"}
                        </TableCell>
                        <TableCell>
                          {isLoading ? (
                            <Skeleton animation="wave"></Skeleton>
                          ) : (
                            <>
                              {e.isPlaceholder ? (
                                <>vite_</>
                              ) : (
                                <AddressExplorerLink address={e.address} startPosition={matches ? 10 : 7}></AddressExplorerLink>
                              )}
                            </>
                          )}
                        </TableCell>
                        <TableCell align="right">
                          {isLoading ? (
                            <Skeleton animation="wave"></Skeleton>
                          ) : (
                            <>
                              {e.isPlaceholder ? (
                                <>-</>
                              ) : (
                                <Link underline="hover" component={RouterLink} to={`/punk/${e.punkId}`} target="_blank">#{e.punkId}</Link>
                              )}
                            </>
                          )}
                        </TableCell>
                        <TableCell align="right">
                          {isLoading ? (
                            <Skeleton animation="wave"></Skeleton>
                          ) : (
                            <>
                              {e.isPlaceholder ? (
                                <Chip size="small" label="-" />
                              ) : (
                                <Chip size="small" label={e.rank} />
                              )}
                            </>
                          )}
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
              <Paper elevation={3} sx={{ p: 2, textAlign: "center", mt: 2 }}>
                <Countdown date={date.toISOString()}></Countdown>
              </Paper>
            </Box>
          </Box>
        </Paper>
      </Box>
    </Box>
  );
}
